.drink-slide {
	position: relative;
	min-height: fit-content;
	max-height: 100%;
	max-height: 80vh;
	max-height: 60vw;
	max-height: 550px;
	overflow-y: auto;
	overflow-x: visible;
	padding: 0 20px 70px 20px;
	margin: 10px 0;
	padding: 20px 20px 20px 30px;
	border-radius: 50px;
	border: 0px solid var(--Others-Background);
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	&__img {
		border-radius: 50px;
		margin-right: 10px;
		border: 1px solid var(--Others-Background);
		max-width: 20vw;
		max-height: 20vw;
		overflow: hidden;
	}
	&__text {
		display: flex;
		flex-direction: column;
		width: 50vw;
		text-align: start;
		&_bold {
			font-weight: bold;
		}
	}
	&__ingridients {
		margin: 15px 5% 0 5%;
		width: 90%;
		padding-bottom: 50px;
		display: flex;
		gap: 15px;
		justify-content: center;
		flex: 1 0 min-content;
		align-items: center;
		align-content: center;
		flex-wrap: wrap;
	}
	&__ingridient-wrapper {
		display: block;

		padding: 5px;
		border-radius: 10px;
		padding: 0 50px;
		width: max-content;
	}
	&__ingridient {
		position: relative;
		width: 100%;
		max-height: 100%;
		&:first-child {
			text-shadow: 0px 0px 10px black;
		}
		&:hover {
			.drink-slide__ingridient {
				display: block;
			}
		}
	}
}

@media (max-width: 800px) {
	.drink-slide {
		&__text {
			width: 100vw;
		}
		&__ingridients {
			width: 100vw;
		}
	}
}

@media (orientation: portrait) and (max-width: 800px) {
	.drink-slide {
		max-height: max-content;
		padding: 20px;

		&__img {
			border-radius: 50px;
			max-width: 80vw;
			max-height: 90vw;
			margin-bottom: 15px;
			overflow: hidden;
		}
	}
}
